.main-header {
  padding: 90px 0;
  background-color: @header-background;

  @media (max-width: 767px) {
    padding: 30px 0;
  }

  p {
    color: #ffffff;
    margin: 0;
    margin-bottom: 10px;
    font-size: 36px;
    font-weight: 700;
    max-width: 690px;

    @media (max-width: 767px) {
      font-size: 16px;
      margin-bottom: 0;
    }
  }

  span {
    color: #ffffff;
    font-size: 45px;
    font-weight: 700;

    @media (max-width: 767px) {
      font-size: 20px;
    }
  }

  &__math {
    margin-right: 30px;
  }
}

.main-header__section {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.main-header__images {
  @media (max-width: 767px) {
    display: none;
  }
}
