.main-footer {
  padding: 50px 0;
  background-color: @header-background;

  @media (max-width: 767px) {
    padding: 30px 0;
  }

  ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 767px) {
      flex-direction: column;
    }
  }

  li {
    width: 100%;
    transition: all 0.6s ease-in-out;

    &:hover {
      transform: scale(1.1);
    }

    @media (max-width: 767px) {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  a {
    color: #ffffff;
    font-size: 20px;
    transition: all 0.6s ease-in-out;

    span {
      margin-left: 20px;
    }

    &:hover {
      opacity: 0.6;
    }

    @media (max-width: 767px) {
      width: 100%;
      display: flex;
      justify-content: space-between;
      font-size: 16px;
    }
  }
}
