@font-face {
  font-family: "Exo2";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Exo2-Regular.woff2") format("woff2"), url("../fonts/Exo2-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Exo2";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Exo2-Bold.woff2") format("woff2"), url("../fonts/Exo2-Bold.woff") format("woff");
  font-display: swap;
}
body {
  margin: 0;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
  cursor: pointer;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  font-family: "Exo2", "Arial", sans-serif;
}
body {
  font-family: "Exo2", "Arial", sans-serif;
  font-style: normal;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header {
  padding: 90px 0;
  background-color: #2CA4F1;
}
@media (max-width: 767px) {
  .main-header {
    padding: 30px 0;
  }
}
.main-header p {
  color: #ffffff;
  margin: 0;
  margin-bottom: 10px;
  font-size: 36px;
  font-weight: 700;
  max-width: 690px;
}
@media (max-width: 767px) {
  .main-header p {
    font-size: 16px;
    margin-bottom: 0;
  }
}
.main-header span {
  color: #ffffff;
  font-size: 45px;
  font-weight: 700;
}
@media (max-width: 767px) {
  .main-header span {
    font-size: 20px;
  }
}
.main-header__math {
  margin-right: 30px;
}
.main-header__section {
  display: flex;
  flex-direction: row;
  align-items: center;
}
@media (max-width: 767px) {
  .main-header__images {
    display: none;
  }
}
.site-section {
  padding: 120px 0;
}
.site-section h2 {
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 30px;
}
@media (max-width: 767px) {
  .site-section h2 {
    font-size: 24px;
  }
}
.site-section p {
  font-size: 18px;
}
@media (max-width: 767px) {
  .site-section {
    padding: 30px 0;
  }
}
.background-light {
  background-color: #f8f9fa;
}
.header-nav {
  padding: 40px 0;
}
.header-nav:not(:last-child) {
  padding: 40px 0 20px;
}
.header-nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
}
@media (max-width: 767px) {
  .header-nav ul {
    flex-direction: column;
  }
}
.header-nav li:not(:last-child) {
  margin-right: 20px;
}
@media (max-width: 767px) {
  .header-nav li:not(:last-child) {
    margin-right: 0;
    margin-bottom: 30px;
  }
}
.header-nav a {
  border: 2px solid #2CA4F1;
  border-radius: 10px;
  padding: 10px;
  background-color: #ffffff;
  color: #2CA4F1;
  transition: all 0.6s ease-in-out;
}
.header-nav a:hover {
  background-color: #2CA4F1;
  color: #ffffff;
}
.credo {
  background-color: #2CA4F1;
}
.credo h2 {
  color: #ffffff;
  margin-bottom: 30px;
}
.credo p {
  padding: 0;
  margin: 0;
  font-style: italic;
  font-size: 36px;
}
@media (max-width: 767px) {
  .credo p {
    font-size: 18px;
  }
}
.credo__text {
  padding: 30px;
  border-radius: 20px;
  border: 2px solid #ffffff;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .credo img {
    display: none;
  }
}
.links li {
  transition: all 0.6s ease-in-out;
}
.links li:not(:last-child) {
  margin-bottom: 30px;
}
.links li:hover {
  opacity: 0.6;
}
.links a {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #2CA4F1;
}
.links a img {
  margin-right: 20px;
}
.main-footer {
  padding: 50px 0;
  background-color: #2CA4F1;
}
@media (max-width: 767px) {
  .main-footer {
    padding: 30px 0;
  }
}
.main-footer ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .main-footer ul {
    flex-direction: column;
  }
}
.main-footer li {
  width: 100%;
  transition: all 0.6s ease-in-out;
}
.main-footer li:hover {
  transform: scale(1.1);
}
@media (max-width: 767px) {
  .main-footer li:not(:last-child) {
    margin-bottom: 20px;
  }
}
.main-footer a {
  color: #ffffff;
  font-size: 20px;
  transition: all 0.6s ease-in-out;
}
.main-footer a span {
  margin-left: 20px;
}
.main-footer a:hover {
  opacity: 0.6;
}
@media (max-width: 767px) {
  .main-footer a {
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
  }
}
