.links {
  li {
    transition: all 0.6s ease-in-out;

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    &:hover {
      opacity: 0.6;
    }
  }

  a {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: @header-background;

    img {
      margin-right: 20px;
    }
  }
}
